import { useEffect } from 'react';
import { Helmet, history, useArticle, useSelector } from 'topwrite';
import '../scss/app.scss';
import Article from '@/components/article';

declare global {
    interface Window {
        __wxjs_environment: any;
        wx: any;
    }
}

export default function App() {
    const file = useSelector('file');
    const { summary } = useSelector('book');

    const article = useArticle();

    useEffect(() => {
        if (!article) {
            const firstPath = summary.getFirstPath();
            if (firstPath) {
                history.replace(file.relative(firstPath));
            }
        }
    }, [article]);

    if (!article) {
        return null;
    }

    return <>
        <Helmet>
            {window.__wxjs_environment === 'miniprogram' &&
            <script type='text/javascript' src='https://res.wx.qq.com/open/js/jweixin-1.3.2.js'></script>}
            <meta name='viewport'
                  content='width=device-width, initial-scale=1, maximum-scale=1, user-scalable=no, viewport-fit=cover' />
        </Helmet>
        <Article article={article} />
    </>;
}
