import { styled } from 'topwrite';
import { ReactComponent as FontPlus } from '@/images/font+.svg';
import { ReactComponent as FontMinus } from '@/images/font-.svg';

interface FontProps {
    font: number;
    onFontChange: (font: number) => void;
}

export default function Font({ font, onFontChange }: FontProps) {
    return <Container>
        <FontMinus onClick={() => onFontChange(font - 1)} />
        <Line />
        <Main>
            <span>当前</span>
            <span>{font * 2}号字</span>
        </Main>
        <Line />
        <FontPlus onClick={() => onFontChange(font + 1)} />
    </Container>;
}

const Main = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  span {
    font-size: 11px;
    margin-bottom: 4px;

    &:last-child {
      margin-bottom: 0;
    }
  }
`;

const Line = styled.div`
  border-top: 1px dotted var(--mina-color);
  height: 0;
  flex: 1;
  margin: 0 15px;
`;

const Container = styled.div`
  height: 87.5px;
  display: flex;
  align-items: center;
  padding: 0 50px;

  svg {
    width: 55px;
    height: 32px;
    color: var(--mina-color);
  }
`;
