import { Link, styled, SummaryArticle, useNextArticle, usePrevArticle, useThemeContext } from 'topwrite';
import { ReactComponent as MenuIcon } from '../images/menu.svg';
import { ReactComponent as StarIcon } from '@/images/star.svg';
import { ReactComponent as MoonIcon } from '@/images/moon.svg';
import { ReactComponent as SunIcon } from '@/images/sun.svg';
import { ReactComponent as SettingIcon } from '@/images/setting.svg';
import Summary from '@/components/summary';
import Font from '@/components/font';

interface BarProps {
    article: SummaryArticle;
    mode: string;
    onModeChange: (mode: string) => void;
    font: number;
    onFontChange: (font: number) => void;
}

export default function Bar({ article, mode, onModeChange, font, onFontChange }: BarProps) {

    const next = useNextArticle();
    const prev = usePrevArticle();

    const { isDarkTheme, setDarkTheme, setLightTheme } = useThemeContext();

    if (mode === 'summary') {
        return <Summary onHide={() => onModeChange('mini')} />;
    } else if (mode === 'font') {
        return <Container>
            <Font font={font} onFontChange={onFontChange} />
        </Container>;
    }

    return <Container>
        <Menu>
            <Nav>
                {prev ? <NavItem to={prev}>上一章</NavItem> : <NavItem as='a'>上一章</NavItem>}
                <Title><StarIcon /><span>{article.title}</span><StarIcon /></Title>
                {next ? <NavItem to={next}>下一章</NavItem> : <NavItem as='a'>下一章</NavItem>}
            </Nav>
            {mode === 'full' &&
            <Tabs>
                <Tab onClick={() => onModeChange('summary')}>
                    <MenuIcon />
                    <span>目录</span>
                </Tab>
                {isDarkTheme ?
                    <Tab onClick={setLightTheme}>
                        <SunIcon />
                        <span>白天</span>
                    </Tab> :
                    <Tab onClick={setDarkTheme}>
                        <MoonIcon />
                        <span>夜间</span>
                    </Tab>
                }
                <Tab onClick={() => onModeChange('font')}>
                    <SettingIcon />
                    <span>设置</span>
                </Tab>
            </Tabs>}
        </Menu>
    </Container>;

}
const Title = styled.div`
  font-size: 12px;
  display: flex;
  align-items: center;
  flex: 1;
  justify-content: center;

  svg {
    width: 12px;
    height: 12px;
  }

  span {
    margin: 0 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
`;

const NavItem = styled(Link)`
  font-size: 16px;
  padding: 0 18px;
  color: rgba(192, 192, 192, 1);
  text-decoration: none;

  [data-theme="dark"] & {
    color: rgba(57, 69, 82, 1);
  }


  &[href] {
    color: var(--mina-color);
  }
`;

const Nav = styled.div`
  height: 53.5px;
  display: flex;
  align-items: center;
`;

const Tab = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;

  span {
    font-size: 9px;
  }

  svg {
    margin-bottom: 5px;
    width: 24px;
    height: 24px;
  }

  &:first-child {
    width: 100px;
    flex-grow: 0;
    flex-shrink: 0;

    svg {
      width: 24px;
      height: 20px;
      margin-bottom: 7px;
    }
  }

  &:last-child {
    width: 100px;
    flex-grow: 0;
    flex-shrink: 0;

    svg {
      width: 24px;
      height: 17.3px;
      margin-bottom: 8.5px;
    }
  }
`;

const Tabs = styled.div`
  height: 87.5px;
  display: flex;
`;

const Menu = styled.div`

`;

const Container = styled.div`
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  background: #FFFFFF;
  box-shadow: 0px -5px 20px rgba(0, 0, 0, 0.05);
  z-index: 1000;
  padding-bottom: 0;
  padding-bottom: env(safe-area-inset-bottom);

  [data-theme="dark"] & {
    background: rgba(16, 20, 24, 1)
  }
`;
